import { useEffect } from "react";
import { gsap } from "gsap";

export default function Section3() {
  useEffect(() => {
    let tl = gsap.timeline({
      scrollTrigger: { trigger: ".third", start: "top center" },
    });

    let divs = document.querySelectorAll(".third div");

    tl.set(divs, { visibility: "visible" });
    tl.fromTo(
      divs,
      {
        clipPath: "polygon(0 0, 100% 0, 100% 0, 0% 0%)",
        webkitClipPath: "polygon(0 0, 100% 0, 100% 0, 0% 0%)",
      },
      {
        clipPath: "polygon(0 0, 100% 0, 100% 100%, 0 100%)",
        webkitClipPath: "polygon(0 0, 100% 0, 100% 100%, 0 100%)",
        duration: 1.5,
        ease: "power3.out",
        stagger: 0.1,
      }
    );
    return () => {
      tl.scrollTrigger.kill(true);
      tl.pause().kill(true);
    };
  }, []);

  useEffect(() => {
    let tl = gsap.timeline({
      scrollTrigger: {
        trigger: ".third",
        start: "top top",
        toggleActions: "play reverse play reverse",
      },
    });
    tl.to("#third", { "--Width": "110%" });

    return () => {
      tl.scrollTrigger.kill(true);
      tl.pause().kill(true);
    };
  }, []);
  return (
    <section className="third">
      <div id="sofa1"></div>
      <div id="sofa2"></div>
      <div id="sofa3"></div>
      <div id="sofa4"></div>
      <div id="sofa5"></div>
      <div id="sofa__empty"></div>
      <div id="sofa6"></div>
      <div id="sofa__empty"></div>
      <div id="sofa7"></div>
      <div id="sofa8"></div>
    </section>
  );
}
